 position: relative;
 top: -15px;
 display: flex;
 justify-content: center;
}

h1{
 margin-block-start: -7px;
 margin-block-end: .5em;
}

.links{
 display: flex;
 position: relative;
 justify-content: space-evenly;
 align-items: baseline;
 font-family: cheap-pine, sans-serif;
 font-weight: 400;
 font-style: normal;
 font-size: calc(58px + 2vmin);
 text-shadow: -8px 6px 3px rgb(0, 0, 0);
 letter-spacing: 8px;
 top: -23px

}

#about:hover, #portfolio:hover, #contact:hover, #resume:hover {
    transform: scale(0.9);
    transition-duration: 0.6s;
    /* text-decoration: underline 4px dotted; */
    /* border-bottom: 5px dotted; */
    /* padding-bottom: 1px; */
    cursor: pointer;
}

#about:onClick{
    transform: scale(1.3);
    border-bottom: 5px dotted;
}


